var React = require('react');
var ReactRouter = require('react-router');
var Link = ReactRouter.Link;

var Home = React.createClass({
  render: function () {
    return (
      <div>
        <div className='jumbotron col-sm-12 text-center'>
          <h1>Keyboard</h1>
          <p className='lead'> If I were not a physicist, I would probably be a musician. I often think in music. I live my daydreams in music. I see my life in terms of music.  Albert Einstein </p>
          <Link to='/playMusic'>
            <button type='button' className='btn btn-lg btn-success'>Make music</button>
          </Link>
        </div>
        <p>Velkominn  sun key-board.herokuapp.com essi sa var binn til af skar lafssyni </p>
        <p> Hr er hgt a spila  virtual lyklabor og leika sr me effect  v, etta verkefni var unni 9-12 Febrar 2017  </p>
        <p>essi sa notast vi <a href="https://tonejs.github.io/">Tone.js</a> frameworki til ess a spila hlj og gera effecta san sjlf notast lka vi React  framendanum, san sjlf er aalega framendi v ekki var neinn tilgangur fyrir v a hafa bakenda  henni</p>
      </div>
    )
  }
});

module.exports = Home;



